.homepage-div  {
	min-height: 100vh; /* will cover the 100% of viewport */
 	overflow: hidden;
 	display: block;
 	position: relative;
 	padding-bottom: 100px;
    -webkit-animation: fadein 3s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
            animation: fadein 3s;
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Firefox < 16 */

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Internet Explorer */

/* Opera < 12.1 */

nav {
	display: -ms-flexbox;
	display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    position: fixed;
    width: 100%;
	top: 0;
	background: white;
	z-index: 100;
	-webkit-box-shadow: 0 6px 6px -6px black;
	        box-shadow: 0 6px 6px -6px black;
}

.nav-p {
	margin-top: 16px;
}

.nav-link {
	text-decoration: none;
}

header {
	background: url('./images/hands.jpg');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	height: 40em;
    -webkit-filter: contrast(78.9%);
            filter: contrast(78.9%);
   	-webkit-box-shadow: 0 17px 54px -11px black;
   	        box-shadow: 0 17px 54px -11px black; 
}

.header-text {
	font-size: 70px;
	line-height: 77px;
	text-align: center;
	padding-top: 4%;
	color: white;
}

.sub-title {
	font-size: 50px;
}

.info {
	background: url('./images/crossword.png');
	padding: 70px;
	text-align: center;
	font-size: 20px;
	line-height: 30px;
}

.history {
	margin: 0px 100px;
}

.time {
	font-size: 30px;
}

.location {
	background: url('./images/location.jpg');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	height: 40em;
}

.location-info {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	padding-top: 55px;
}

.address {
	text-align: center;
	padding-top: 50px;
	font-size: 30px;
	color: white;
}

.location-photo {
	margin-right: 20px;
	height: 300px;
}

@media only screen and (max-width: 768px) {

	header {
		height: 28em;
	}

	.header-text {
	    font-size: 58px;
	    line-height: 59px;
	    padding-top: 8%;
	}

	.sub-title {
	    font-size: 40px;
	}

	.history {
	    margin: 0px;
	}

	.address {
	    font-size: 41px;
	}

	.location {
	    height: 57em;
	}

	.location-info {
	    display: block;
	    margin-left: 25%;
	    padding-top: 0px;
	}

	.location-photo {
	    margin: 20px;
	    height: 300px;
	}
}

@media only screen and (max-width: 450px) {

	header {
	    height: 19em;
	    -webkit-filter: contrast(100%);
	            filter: contrast(100%);
	}

	.header-text {
	    font-size: 47px;
	    padding-top: 14%;
	}

	.sub-title {
	    font-size: 27px;
	}

	.info {
	    padding: 40px;
	    font-size: 18px;
	    line-height: 25px;
	}

	.address {
	    font-size: 28px;
	    padding-top: 20px;
	}

	.location-info {
	    margin-left: -3%;
	}

}












